@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

/* Comportamiento hover para proyectos del carrusel */
.proyecto-card:hover img {
  transform: scale(1.1);
}

.proyecto-card:hover .proyecto-info {
  max-height: 100px;
  opacity: 1;
}

/* Comportamiento hover para proyectos de la cuadr√≠cula */
.proyecto-grid-item:hover .proyecto-overlay {
  background-color: rgba(30, 58, 138, 0.1);
}

.proyecto-grid-item:hover .proyecto-title {
  transform: translateY(0);
}

a:hover {
  text-decoration-color: #FBBF24;
}

a {
  text-decoration: underline;
  text-decoration-color: #000000;
}

.scroll-button {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background-color: #FBBF24;
  color: #000000;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border: none;
  cursor: pointer;
  z-index: 90;
  transition: opacity 0.4s ease;
  opacity: 1;
}

.scroll-button.hidden {
  opacity: 0;
  
  pointer-events: none;
}


    .btn-suscribirse {
      background-color: #000000;
      color: white;
      border: none;
      padding: 1rem 1.5rem;
      cursor: pointer;
      font-weight: bold;
      border-radius: 0 2rem 2rem 0;
      transition: all 0.3s;
    }

    @media (max-width: 640px) {
      .btn-suscribirse {
        border-radius: 0 0 1rem 1rem;
      }
    }

    .btn-suscribirse:hover {
      background-color: #FBBF24;
      color: black;
    }

    .btn-yellow-subscribe {
      background-color: #FBBF24;
      color: #F0F0F0;
      padding: 0.75rem 1rem;
      border-radius: 0.5rem;
      font-weight: bold;
      border: none;
      cursor: pointer;
      transition: all 0.3s;
    }

    .btn-yellow-subscribe:hover {
      background-color: #d4a116;
      color: #000000;
    }

    .social-icon {
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.1);
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      transition: all 0.3s;
    }

    .social-icon:hover {
      background-color: #FBBF24;
      color: #000000;
    }

    .sip-card {
      border-radius: 1rem;
      overflow: hidden;
      box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
      transform: rotate(2deg);
      transition: transform 0.4s ease, box-shadow 0.4s ease;
      width: 100%;
      aspect-ratio: 16 / 10;
      background-color: #F3F4F6;
    }

    .sip-card:hover {
      transform: rotate(0deg) scale(1.02);
      box-shadow: 0 25px 30px -5px rgba(0, 0, 0, 0.2);
    }

    .sip-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }

    .gallery-item {
      border-radius: 1rem;
      overflow: hidden;
      box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
      transition: transform 0.4s ease, box-shadow 0.4s ease;
      height: 100%;
    }

    .gallery-item:hover {
      transform: scale(1.03);
      box-shadow: 0 25px 35px -5px rgba(0, 0, 0, 0.2);
    }

    .gallery-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }

    @keyframes modalFadeIn {
      from {
        opacity: 0;
        transform: scale(0.8) translateY(30px);
      }
      to {
        opacity: 1;
        transform: scale(1) translateY(0);
      }
    }
    
    @keyframes backdropFadeIn {
      from {
        opacity: 0;
        backdrop-filter: blur(0px);
      }
      to {
        opacity: 1;
        backdrop-filter: blur(5px);
      }
    }
    
    @keyframes pulse {
      0% {
        box-shadow: 0 0 0 0 rgba(37, 211, 102, 0.5);
      }
      70% {
        box-shadow: 0 0 0 10px rgba(37, 211, 102, 0);
      }
      100% {
        box-shadow: 0 0 0 0 rgba(37, 211, 102, 0);
      }
    }